import {common} from "../../utils/api/url";
import mixins from "../../utils/mixins";
import MyNormalDialog from "../../components/utils/dialog/MyNormalDialog";
import DialogNormal from "../../components/utils/dialog/DialogNormal";
import {insterestText, MessageError, MessageSuccess} from "../../utils/utils";
import buildValidater from "../../utils/validater/buildValidater";
export default {
  mixins: [mixins, buildValidater],
  components: {MyNormalDialog,DialogNormal},
  data(){
    return {
      tableData: [],
      modalVisible: false,
      dialogLoading: false,
      visibleConfim: false,
      subTitle: '',
      form: {
        id: '',
        no: '',
        name: '',
        areaTotal: '0',
        insterest: '1',
        createTime: '',
        person: '',
        floorTotal: '',
        startFloor: '',
        phone: ''
      }
    }
  },
  created() {
    this.init();
  },
  methods: {
    init(){
      let params = {
        buildingType: 0
      };
      this.$axios(common.build_list, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data;
        }
      });
    },
    addInfo(){
      this.modalVisible = true;
    },
    editInfo(row){
      this.form = {
        id: row.id,
        no: row.building_no,
        name: row.building_name,
        areaTotal: '0',
        insterest: '1',
        createTime: this.$moment().format("YYYY-MM"),
        person: row.person,
        floorTotal: row.storey,
        startFloor: row.start_storey,
        phone: row.phone,
      };
      this.modalVisible = true;
    },
    deleteInfo(row){
      this.form.id = row.id;
      this.subTitle = row.building_name;
      this.visibleConfim = true;
    },
    insterestInfo(val){
      return insterestText(val);
    },
    okDialog(event){
      let url = "";
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            buildingNo: this.form.no,
            buildingName: this.form.name,
            area: this.form.areaTotal,
            propertyRight: this.form.insterest,
            createTime: this.$moment().format("YYYY-MM"),
            storey: this.form.floorTotal,
            person: this.form.person,
            phone: this.form.phone,
            status: 1,
            startStorey: this.form.startFloor,
            type: 0
          };
          if (this.form.id != ""){
            url = common.build_update;
            params['buildingId'] = this.form.id;
          }else {
            url = common.build_add;
          }
          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.modalVisible = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    closeDialog(event){
      this.form = {
        id: '',
        no: '',
        name: '',
        areaTotal: 0,
        insterest: '1',
        createTime: '',
        person: '',
        floorTotal: '',
        startFloor: '',
        phone: ''
      };
      this.subTitle = "";
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    cancelDialog(){
      this.modalVisible = false;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        buildingId: this.form.id
      };
      url = common.build_delete;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    }
  }
}
